* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f9f9f9;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body {
  height: 100vh;
}

.topNav {
  background-color: #EFF5F5;
  box-shadow: 0.1px 0.1px 3px rgba(0, 0, 0, 0.212), 0.1px 0.1px 5px rgba(0, 0, 0, 0.059);
  margin-bottom: 10px;
}
.topNav ul li {
  list-style-type: none;
}
.topNav ul li :hover {
  background-color: #D6E4E5;
}
.topNav ul li a {
  width: 100%;
  height: 40px;
  display: block;
  color: #497174;
  text-decoration: none;
  padding: 12px 20px;
}
.topNav ul li .small-icon {
  font-size: 20px;
  margin-right: 10px;
}
.topNav ul li a {
  display: inline-flex;
  align-items: center;
}
@media screen and (min-width: 555px) {
  .topNav ul {
    display: inline-flex;
    width: 100%;
    justify-content: center;
  }
  .topNav ul .hidden-on-desktop {
    display: none;
  }
}
.topNav .upper-menu {
  display: flex;
  justify-content: space-between;
  white-space: nowrap;
}

.hidden-menu {
  height: 40px;
  overflow: hidden;
}

.icon {
  line-height: 0;
  font-size: 30px;
  margin: -10px;
}

.form-container {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 0 10px;
  overflow-y: auto;
}
.form-container .new-post-form {
  background-color: #f9f9f9;
  border: 1px solid black;
  margin-top: 5px;
  border-radius: 10px;
  display: block;
  padding: 10px;
  width: 60%;
  max-width: 500px;
  min-width: 400px;
}

.input {
  padding: 10px;
  font-size: 1.1rem;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid black;
}

.text-label {
  max-width: 100%;
  min-width: 100%;
  max-height: 100%;
  min-height: 250px;
}

.button {
  padding: 5px 10px;
  border-radius: 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
}

.primary {
  background-color: #497174;
}

.primary:hover {
  background-color: #497174;
}

.large-button {
  width: 150px;
  height: 35px;
}

.hidden {
  display: none;
}

@media screen and (max-width: 555px) {
  .form-container {
    padding: 0px !important;
  }
  .new-post-form {
    height: 100%;
    width: 100% !important;
    min-width: auto !important;
    border-radius: 0 !important;
  }
}
.spaced-container {
  text-align: center;
}

.canvas {
  width: 500px;
  height: 300px;
  background-color: #fff;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.275);
}

.container {
  width: 100%;
  height: 100vh;
  padding: 5px;
}

.table, th, td, tr {
  border: 1px solid black;
  border-collapse: collapse;
  width: 100%;
}
.table td, .table th, th td, th th, td td, td th, tr td, tr th {
  padding: 10px;
}

.feed-menu {
  display: flex;
  width: 100%;
  justify-content: center;
}
.feed-menu .link-button {
  margin-right: 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid rgb(128, 128, 128);
  outline: 0;
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
}
.feed-menu .link-button:hover {
  background-color: #eee;
}
.feed-menu .link-button:active {
  background-color: rgb(224, 223, 223);
}
.feed-menu .link-button.active {
  background-color: #EFF5F5;
}

.card-container {
  margin-top: 10px;
  display: flex;
  position: relative;
}

.full-card-design {
  position: relative;
}

.full-card-design:hover .card-back, .card-back:hover .card-back, .card:hover .card-back {
  background-color: #BA94D1;
  cursor: pointer;
}
.full-card-design:hover .card, .card-back:hover .card, .card:hover .card {
  cursor: pointer;
  background-color: rgb(234, 233, 233);
}
.full-card-design:hover .card .card-ring::before, .card-back:hover .card .card-ring::before, .card:hover .card .card-ring::before {
  background-color: rgb(234, 233, 233);
}

.card-top {
  padding-top: 5px;
}

.card-back {
  position: absolute;
  top: 5px;
  left: 4px;
  transform: rotate(-2deg);
  z-index: 1;
  background-color: #DEBACE;
  height: 200px;
  width: 150px;
  border: 0.1px solid black;
}

.card {
  position: relative;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #fff;
  border: 1.5px solid black;
  padding: 5px;
  margin-right: 20px;
  height: 200px;
  width: 150px;
}
.card .card-rings {
  display: flex;
  justify-content: space-evenly;
  width: 108%;
  position: absolute;
  top: -10px;
  left: -10px;
}
.card .card-ring {
  width: 20px;
  height: 20px;
  border: 3px solid rgb(43, 43, 43);
  border-radius: 50px;
  position: relative;
}
.card .card-ring::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 10px;
  background-color: #fff;
  top: 6.5px;
  left: 5px;
}
.card .card-ring::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: rgb(24, 24, 24);
  box-shadow: 0.1px 0.1px 2px rgba(0, 0, 0, 0.529);
  border-radius: 30px;
  top: 12px;
  left: 3px;
}

.card-content .card-title {
  font-weight: 600;
}/*# sourceMappingURL=style.css.map */