*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: monospace;
}

//variables
$dasf: #eee;
$bg-white: #f9f9f9;
$nav-bg:#EFF5F5;
$card-yellow: #FBFACD;
$card-pink-1: #DEBACE;
$card-pink-2: #BA94D1;
$card-pink-3: #7F669D;

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: $bg-white;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body{
  height: 100vh;
}



.topNav{
  background-color: $nav-bg;
  box-shadow: 0.1px 0.1px 3px rgba(0, 0, 0, 0.212), 0.1px 0.1px 5px rgba(0, 0, 0, 0.059);
  margin-bottom: 10px;

  ul{
   

    li {
      // padding: 10px;
      // text-align: center;
      list-style-type: none;
      :hover{
        background-color: #D6E4E5;  
      }

      a {
        width: 100%;
        height: 40px;
        display: block;
        color: #497174;
        text-decoration: none;
        padding: 12px 20px;

      }
      
      .small-icon {
        font-size: 20px;
        margin-right: 10px;
      }

      a{
        display: inline-flex;
        align-items: center;
      }

    }


     //visible only on desktop
     @media screen and (min-width: 555px){
      display: inline-flex;
      width: 100%;
      justify-content: center;

      .hidden-on-desktop{
        display: none;
      }
      
    }
  }

  .upper-menu{
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
  }

}


.hidden-menu{
  height: 40px;
  overflow: hidden;
}

.icon{
  line-height: 0;
  font-size: 30px;
  margin: -10px;
}



//form and inputs

.form-container{
  // position: absolute;
  // top: 0;
  // left: 0;
  // background-color: rgba(15, 15, 15, 0.433);
  width: 100%;
  height: 100%;
  display: flex;
  padding: 0 10px;
  overflow-y: auto;
  // align-items: center;
  // justify-content: center;

  .new-post-form{
    background-color: $bg-white;
    border: 1px solid black;
    margin-top: 5px;
    border-radius: 10px;
    display: block;
    padding: 10px;
    width: 60%;
    max-width: 500px;
    min-width: 400px;
  }
  

}

.input {
  padding: 10px;
  font-size: 1.1rem;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid black;
}
.text-label{
  max-width: 100%;
  min-width: 100%;
  max-height: 100%;
  min-height: 250px;
}
.button {
  padding: 5px 10px;
  border-radius: 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
} 
.primary{
  background-color: #497174;
}

.primary:hover{
  background-color: #497174;
}

.large-button{
  width: 150px;
  height: 35px;
}

.hidden {
  display: none;
}

 //visible only on mobile
 @media screen and (max-width: 555px){
  .form-container {
    padding: 0px !important;
  }
  .new-post-form{
    height: 100%;
    width: 100% !important;
    min-width: auto !important;
    border-radius: 0 !important;
  }
 }


 .spaced-container{
  text-align: center;
  // justify-content: center;
}

.canvas {
  width: 500px;
  height: 300px;
  background-color: #fff;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.275);
}





.container{
  width: 100%;
  height: 100vh;
  padding: 5px;
}


.table, th, td, tr{
  border: 1px solid black;
  border-collapse: collapse;
  width: 100%;
  td, th {
    padding: 10px;
  }
}


.feed-menu{
  display: flex;
  width: 100%;
  justify-content: center;

  .link-button {
    margin-right: 10px;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid rgb(128, 128, 128);
    outline: 0;
    cursor: pointer;
    padding: 10px;
    border-radius: 5px;
  }
  .link-button:hover{
    background-color: #eee;
  }
  .link-button:active{
    background-color: rgb(224, 223, 223);
  }
  .link-button.active{
    background-color: $nav-bg;
  }
}


//cards
.card-container{
  margin-top: 10px;
  display: flex;
  position: relative;
}
.full-card-design{
  position: relative;
}

.full-card-design:hover, .card-back:hover, .card:hover{
  .card-back{
    background-color: $card-pink-2;
    cursor: pointer;
  }
  .card{
    cursor: pointer;
    background-color: rgb(234, 233, 233);
    .card-ring::before{
      background-color: rgb(234, 233, 233);
    }
  }
}

.card-top{
  padding-top: 5px;
}

.card-back{
  position: absolute;
  top: 5px;
  left: 4px;
  transform: rotate(-2deg);
  z-index: 1;
  background-color: $card-pink-1;
  height: 200px;
  width: 150px;
  border: 0.1px solid black;
}
.card {
  position: relative;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #fff;
  border: 1.5px solid black;
  padding: 5px;
  margin-right: 20px;
  // border-radius: 5px;
  height: 200px;
  width: 150px;

  .card-rings {
    display: flex;
    justify-content: space-evenly;
    width: 108%;
    position: absolute;
    top: -10px;
    left: -10px;
  }
  .card-ring{
    width: 20px;
    height: 20px;
    // background-color: red;
    border: 3px solid rgb(43, 43, 43);
    border-radius: 50px;
    position: relative;
  }
  .card-ring::before{
    content: "";
    position: absolute;
    width: 15px;
    height: 10px;
    background-color: #fff;
    top: 6.5px;
    left: 5px;
  }
  .card-ring::after{
    content: "";
    position: absolute;
    width: 5px;
    height: 5px;
    background-color: rgb(24, 24, 24);
    box-shadow: 0.1px 0.1px 2px rgba(0, 0, 0, 0.529);
    border-radius: 30px;
    top: 12px;
    left: 3px;
  }
}

.card-content{
  .card-title{
    font-weight: 600;
  }
}